import React from 'react';
import styled from 'styled-components';

export default function SectionContact () {
  return (
    <ContactWrapper>
      <section className="py-5" id="contactme">
        <div className="row">
          <div className="col-10 mx-auto col-md-6 my-3">
            <h2>Contato</h2>
            <form
              className="mt-5"
              action="https://formspree.io/mzbgdgzg"
              method="POST"
            >
              <div className="form-group">
                <input
                  type="text"
                  name="firsName"
                  className="form-control"
                  placeholder="Seu nome"
                />
              </div>
              {/* message */}
              <div className="form-group">
                <input
                  type="email"
                  name="email"
                  className="form-control"
                  placeholder="Seu e-mail para contato"
                />
              </div>
              {/* message */}
              <div className="form-group">
                <input
                  type="text"
                  name="subject"
                  className="form-control"
                  placeholder="Assunto da mensagem"
                />
              </div>
              {/* message */}
              <div className="form-group">
                <textarea
                  type="form"
                  name="message"
                  className="form-control"
                  rows="5"
                  placeholder="Você pode escrever sua mensagem nesse campo"
                />
              </div>
              <div className="form-group mt-3">
                <input
                  type="submit"
                  value="Enviar"
                  className="form-control bg-primary text-white botao"
                />
              </div>
            </form>
          </div>
        </div>
      </section>
    </ContactWrapper>
  );
}

const ContactWrapper = styled.div`


.botao{
  transition: 1s;
  &:hover{background-color: palevioletred !important;
}}
.row{
  padding-top:15vh;
}
#contactme {

  border-radius: 5px;
  min-height: 100vh;
  margin-top: 0.5vh;
  padding: 0;
background-color: whitesmoke;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }

`;
