import React from 'react';
import styled from 'styled-components';
import ProjectSlider from './ProjectsComponents/ProjectsSlider';

const SectionProjects = () => {
  return (
    <SectionProjectsWrapper>
      <div className="main" id="portfolio">
        <div className="titulo">
          <h1>Projetos</h1>
        </div>
        <ProjectSlider />
        <div className="titulo2">
          <p>
            *Essa seção em dispositives móveis é melhor visualizada com o
            dispositivo "deitado".
          </p>
        </div>
      </div>
    </SectionProjectsWrapper>
  );
};

export default SectionProjects;

const SectionProjectsWrapper = styled.div`
  .main {
    position: relative;
    margin-top: 0.5vh;
    border-radius: 5px;
    background-color: rgba(255,255,255, 0.8);
    padding: 1vh;
  }
  #portfolio {
    border-radius: 5px;
    min-height: 100vh;
    margin-top: 0.5vh;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 112.5%;
    line-height: 1.6em;
  }
  .titulo {
    background-color: white;
    text-align: center;
    border-radius: 5px;
  }
  .titulo2 {
    margin-top:1vh;
    border-radius: 5px;
    background-color: white;
    text-align: left;
    z-index: 6;
  }
`;
